
* {
    box-sizing: border-box; }
.container {
    max-width: 1140px;
    margin: 0 auto;
    position: relative; }
.main-section {
    position: relative;
    min-height: 800px;
    background: #F0F0F0;
    padding-top: 16px;
    overflow: hidden;



    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 69px;
        align-items: center; }

    &__logo {
        height: 69px; }
    &__content-container {
        position: relative;
        display: flex;
        flex-direction: row; }
    &__first-elipse {
        position: absolute;
        top: 129px;
        right: 34px; }
    &__second-elipse {
        position: absolute;
        right: -120px;
        top: 650px; } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row;
        list-style-type: none; }
    &__list-item {
        margin-right: 64px;
        font-size: 18px;
        &:last-child {
            margin-right: 0px; }
        a {
            text-decoration: none;
            color: black; } } }

.left-wrapper {
    position: relative;
    img {
        position: relative;
        z-index: 5; }
    &__rectangle-blue {
        position: absolute;
        top: 150px;
        left: -50px;
        width: 565.95px;
        height: 220px;
        background: #5290EA;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-64deg);
        z-index: 2; }
    &__rectangle-orange {
        position: absolute;
        width: 618.43px;
        height: 220px;
        left: -30px;
        top: 160px;
        background: #F28B64;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-50.81deg);
        z-index: 3; }
    &__rectangle-grey {
        position: absolute;
        width: 608.97px;
        height: 220px;
        left: 10px;
        top: 190px;
        background: #E2E2E2;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        transform: rotate(-36.15deg);
        z-index: 1; } }

.right-wrapper {
    width: 372px;
    position: absolute;
    top: 150px;
    right: 76px;
    z-index: 5;
    &__icons-box {
        margin-bottom: 1px;
        padding-left: 10px; }


    &__title {
        margin-bottom: 5px; }

    &__text {
        font-size: 16px;
        margin-bottom: 25px; } }


.icons {
    display: inline-flex;

    &__item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 40px;
        border: 1px solid #5290EA;
        border-radius: 100px;
        margin-right: 17px;
        &:last-child {
            margin-right: 0px; }
        i {
            font-size: 21px; } } }

.title {
    font-size: 72px;
    position: relative;
    &__hr {
        width: 65px;
        position: absolute;
        bottom: 0px;
        margin: 0;
        border: none;
        border-top: 1px solid black; } }

.buttons {
    display: inline-flex;
    &__main-btn {
        margin-right: 32px;
        width: 200px;
        height: 60px;
        background: #F28B64;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        border: none;
        font-size: 18px;
        color: white; }

    &__icon-btn {
        display: inline-flex;
        align-items: center;
        img {}
        margin-right: 1px; }
    &__text {
        margin-left: 25px;
        font-size: 16px; } }

.elipse {
    width: 194px;
    height: 194px;
    background: linear-gradient(143.92deg, rgba(237, 123, 80, 0.5) 14.85%, rgba(72, 138, 234, 0.5) 53.25%, rgba(44, 44, 44, 0.5) 85.74%);
    border-radius: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    &_dig {
        width: 282px;
        height: 282px; }
    &__subtract {
        width: 129.33px;
        height: 129.33px;
        background: #F0F0F0;
        border-radius: 150px; }
    &_big {
        width: 188px;
        height: 188px; } }
.second-section {
    overflow: hidden;
    min-height: 700px;
    &__container {
        padding-top: 32px;
        position: relative; }
    &__title {
        margin-bottom: 40px; }
    &__cards-box {
        display: inline-flex;
        margin-bottom: 25px; }
    &__cards-item {
        margin-right: 30px;
        &:last-child {
            margin-right: 0px; } }
    &__icons-item {
        &:last-child {
            margin-right: 0; } }
    &__icons-box {
        display: flex;
        justify-content: space-evenly; }
    &__first-elipse {
        position: absolute;
        top: 80px;
        right: -60px;
        z-index: -1; }
    &__second-elipse {
        position: absolute;
        top: 364px;
        right: -60px;
        z-index: -1; } }
.cards-item {
    position: relative;
    height: 360px;
    width: 360px;
    background:  #E2E2E2;
    border-radius: 10px;
    display: flex;
    align-items: flex-end;
    &__img {
        height: 335px; }
    &__text {
        width: 280px;
        font-size: 36px;
        text-align: center;
        transform: rotate(90deg);
        position: absolute;
        top: 144px;
        right: -45px; } }
.cards-item__orange {
    background: #F28B64; }
.cards-item__blue {
    background: #5290EA; }
.icons-item {
    display: inline-flex;
    align-items: center;
    &__img {
        margin-right: 20px;
        width: 80px; }
    &__text {
        font-size: 18px;
        span {
            font-size: 48px; } } }
.third-section {
    position: relative;
    min-height: 640px;
    background: #E2E2E2;
    z-index: 0;
    overflow: hidden;
    &__container {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding-left: 167px;
        padding-top: 46px; }
    &__right-wrapper {
        max-width: 480px; }
    &__right-title {
        margin-bottom: 20px; }
    &__text {
        font-size: 18px;
        margin-bottom: 30px; }
    &__first-elipse {
        position: absolute;
        top: 140px;
        left: 11opx;
        z-index: -1; }
    &__figure {
        position: absolute;
        top: 249px;
        left: 15px;
        z-index: -1; }
    &__second-elipse {
        position: absolute;
        right: -120px;
        top: -120px; } }
.title__hr_top {
    bottom: 75px; }

.footer {
    &__img {
        position: absolute;
        left: -250px;
        bottom: 0px; }
    &__container {
        min-height: 272px;
        display: flex;
        align-items: center;
        justify-content: flex-end; }
    &__title {
        margin-bottom: 20px; } }

@media ( max-width: 1199px  ) {
    .container {
        width: 960px; }
    .left-wrapper {
        img {
            width: 750px; }

        &__rectangle-blue {}
        width: 500px;
        height: 200px;
        &__rectangle-orange {}
        width: 500px;
        height: 200px;
        &__rectangle-grey {}
        width: 500px;
        height: 200px; }
    .right-wrapper {
        right: 20px; }
    .main-section__first-elipse {
        right: 10px; }
    .main-section__second-elipse {
        top: 560px; }

    .cards-item {
        width: 300px;
        height: 300px;
        margin-right: 20px; }
    .cards-item__img {
        height: 270px; }
    .cards-item__text {
        top: 120px;
        font-size: 30px; }
    .third-section__container {
        padding-left: 40px; }
    .third-section__right-wrapper {
        max-width: 450px; } }

@media ( max-width: 991px  ) {
    .container {
        width: 720px; } }

@media ( max-width: 767px  ) {
    .container {
        width: 540px; } }

@media ( max-width: 575px  ) {
    .container {
        width: 100%; } }
